:root {
  --col-blue-brand: #1565c0;
  --col-blue-brand-light: #006ca1;
  --col-red-brand: #c61327;
  --col-darkest: #0f0f0f;
  --col-lightest: #fff;
  --col-lighter: #f5f5f5;
  --col-bg: var(--col-lightest);
  --col-txt: var(--col-darkest);
}

* {
  font-family: 'Roboto', sans-serif;
}

html,
body {
  background-color: #fff;
  background-color: var(--col-bg);
  color: #0f0f0f;
  color: var(--col-txt);
  max-width: 100%;
  overflow-x: hidden;
}

h2 {
  font-size: 1.8em;
}

.carousel {
}

.carousel-item,
.carousel-item > img {
  height: 340px;
}

a,
.btn-link {
  color: #1565c0;
  color: var(--col-blue-brand);
}

a:hover,
.btn-link:hover {
  color: #1565c0;
  color: var(--col-blue-brand);
}

a.nav-link:hover {
  text-decoration: underline;
}
a.nav-link:active {
  text-decoration: none;
}

.dropdown-item.active,
.dropdown-item:active {
  background: none !important;
}

.fadeout {
  -webkit-animation: fadeout 1s forwards;
          animation: fadeout 1s forwards;
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
}

.flash-msg {
  -webkit-animation: fadein 1s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
  animation: fadein 1s;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  font-weight: 300;
  left: 0;
  margin-left: auto;
  margin-right: auto;
  position: fixed;
  right: 0;
  top: 0;
  width: 350px;
  z-index: 9999;
}

.footer {
  background-color: #f5f5f5;
  background-color: var(--col-lighter);
  bottom: 0;
  height: 35px;
  line-height: 35px;
  position: fixed;
  width: 100%;
}

.mynerd-wrapper {
  border: 0px solid red;
}

.tooltip-main {
  background: #f3f3f3;
  border: 1px solid #737373;
  border-radius: 50%;
  color: #737373;
  float: right;
  height: 10px;
  margin: 4px 121px 0 5px;
  text-align: left !important;
  width: 15px;
}

.tooltip-qm {
  float: left;
  margin: -2px 0px 3px 4px;
}

.tooltip-inner {
  background: #ffffff;
  border: 1px solid #737373;
  color: rgb(0, 0, 0, 0.7);
  font-size: 1.15em;
  font-weight: 300;
  height: 75px;
  line-height: 1.15em;
  max-width: 236px !important;
  padding: 10px 15px 10px 20px;
  text-align: left;
}

.tooltip.show {
  opacity: 1;
}

.bs-tooltip-auto[x-placement^='bottom'] .arrow::before,
.bs-tooltip-bottom .arrow::before {
  border-bottom-color: #f00;
  /* Red */
}

.nerd-label {
  letter-spacing: -1px !important;
  -webkit-animation: fadein 1s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
  animation: fadein 1s;
}

.mynerd-wrapper > ul li {
  color: #000;
  font-size: 1.35em;
  font-weight: 200 !important;
  letter-spacing: -1 !important;
  list-style-type: none;
  text-transform: uppercase;
}

.konami-window {
  align-items: center;
  display: flex;
  height: 100vh;
  justify-content: center;
  left: 0;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  z-index: 10000 !important;
}

.konami-window img {
  width: 200px;
  z-index: 100000 !important;
}

.section-responsive-pr {
  padding-right: 45px !important;
}

.section-title {
  border-bottom: 1px solid #0f0f0f;
  border-bottom: 1px solid var(--col-darkest);
  display: block;
  font-size: 1.6em;
  font-weight: 800;
  line-height: 1.3;
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  position: relative;
}

.section-title:before {
  background: #c61327;
  bottom: 0;
  content: '';
  display: block;
  height: 5px;
  left: 0;
  position: absolute;
  width: 25px;
}

.table td {
  font-weight: 300;
}
.table th {
  font-weight: 400 !important;
}

.thumbnail-select {
  border: 2px solid black;
  -webkit-filter: opacity(30%);
          filter: opacity(30%);
}

.thumbnail-unselect {
  border: 2px solid lime;
}

.vertical-center {
  align-items: center;
  display: flex;
  justify-content: center;
}

